@import "../../../sass/consts";

.ads_info_list_page {
  background-color: $bg8;

  .list {
    padding: 0 .5rem 1rem;

    .ads_info {
      display: block;

      .time {
        text-align: center;
        padding: .3rem;

        span {
          line-height: 1.5rem;
          display: inline-block;
          font-size: .6rem;
          color: white;
          background-color: #999;
          padding: 0 .5rem;
          border-radius: .3rem;
        }
      }

      img {
        width: 100%;
        border-radius: .5rem .5rem 0 0;
        display: block;
      }

      .title {
        @extend .single_line;
        background-color: white;
        line-height: 2rem;
        padding-left: 1rem;
        border-radius: 0 0 .5rem .5rem;
      }
    }
  }
}
